































@import "@/styles/main.scss";

.GalleryItem {
  position: relative;
  width: 100%;
  height: 600px;
  overflow: hidden;
  transition: all 0.5s ease;

  &.open {
    height: 800px;
    transform: translateY(-50px);
  }

  .img {
    width: 400px;
    height: 100px;
    background-position: center;
    background-size: cover;
  }

  .title {
    position: absolute;
    top: 50px;
    width: calc(100vw - 10%);
    height: 40px;
    background-color: $secondary;
  }

  .description {
  }
}
