



































@import "@/styles/main.scss";

.Features {
  padding: $h2 $h2 0 $h2;
  background-color: $white;
  @include media(s5) {
    padding: $h4;
  }
  h3 {
    text-align: center;
    margin-left: 0;
  }
  &Title {
    width: 100%;
  }
  &List {
    @include media(s2) {
      display: flex;
      justify-content: space-between;
    }
    dl {
      margin-bottom: $h-2;
      @include media(s0) {
        font-size: $h1;
      }
      @include media(s1) {
        padding: $h1;
        text-align: center;
      }
      @include media(s2) {
        flex-grow: 1;
        max-width: $h7;
        background-color: $tertiary;
      }
      @include media(s4) {
        padding: $h3;
      }
      @include media(s5) {
        padding: $h3;
      }
    }
  }
}
